.homeSection {
    /* border: px solid white; */
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
    padding: 15px;
     
}

/* .homeSection>div{
    border: 1px solid red;
} */
.homeSection>:first-child>img {
    margin-top: 60px;
    width: 300px;
    border: none;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.homeSection>:first-child+div {
    line-height: 35px;
}

.homeSection>:first-child+div>h2 {
    font-size: 35px;
    font-weight: 400;
    text-align: center;
}

.homeSection>:first-child+div>h3 {
    font-size: 24px;
    font-weight: 400;
    text-align: center;
}



/* media query */
/* mobile view */
@media screen and (min-width:280px) and (max-width:426.9px) {
    .homeSection>:first-child>img {
        width: 200px;
    }
    .homeSection>:first-child+div>h2 {
        font-size: 25px;
    }
    .homeSection>:first-child+div>h3 {
        font-size: 20px;
    }
}