.skill {
    width: 100%;
    
}

.childskill {
    display: grid;
   /* border: 1px solid white; */
    grid-template-columns: repeat(5, 8%);
    justify-content: center;
    column-gap: 60px;
    row-gap: 30px;
    margin-top: 50px;
}

.childskill>div {
    /* border: 1px solid white; */
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
  
}

.childskill>div>i {
    border: none;
    width: 60%;
    height: 80%;
}

/* Tablet view */
@media screen and (min-width:427px) and (max-width:768px) {
    .childskill {
        display: grid;
        grid-template-columns: repeat(3, 20%);
        align-items: center;
        column-gap: 0;
        gap: 30px;

    }

    .childskill>div {

        height: 60%;
        text-align: center;
    }
}

/* Mobile view */
@media screen and (min-width:320px) and (max-width:426.9px) {
    .childskill {
        display: grid;
        grid-template-columns: repeat(2, 30%);
        column-gap: 0;
        gap: 25px;
    }
}