.about {
    padding: 15px;
   
}

.about>h2 {
    font-size: 45px;
    font-weight: 400;
    text-align: center;
    text-decoration: underline;
    text-decoration-style: solid;
    margin-bottom: 7vh;
}

.childAboutSection {
    width: 70%;
    margin: auto;


}

.childAboutSection>h3 {
    font-size: 22px;
    font-weight: 400;
    text-align: justify;
    width: 90%;
    letter-spacing: 2px;
    margin: auto;
}

/* media query */

/* tablet view */
@media screen and (min-width:426.95px) and (max-width:768px) {

    .childAboutSection {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .childAboutSection {
        width: 90%;
    }

    .childAboutSection>h3 {
        text-align: center;
        margin: 5%;
    }

}

/* mobile view */
@media screen and (min-width:280px) and (max-width:426.9px) {
    .head {
        font-size: 35px
    }

    .childAboutSection {
        width: 92%;
    }

    .childAboutSection>h3 {
        font-size: 18px;
        text-align: justify;
        margin: 2%;
        letter-spacing: 1px;
    }
    
}