

.Streek{
       display:flex;
       gap: 30px;
       padding: 30px;
       justify-content: center;
}

/* Tablet view */
@media screen and (min-width:427px) and (max-width:768px) {
    .Streek{
        display:flex;
        flex-direction: column;
        gap: 10px;
 }
  }
  /* Mobile view */
  @media screen and (min-width:320px) and (max-width:426.9px) {
    .Streek{
        display:flex;
        flex-direction: column;
        gap: 10px;
 }
    }